<template>
  <el-dialog title="新增加班信息" :visible.sync="dialogAdd.show">
    <el-form :model="formaddData" label-width="100px" :rules="formaddRules" ref="formadd">
      <el-form-item label="项目" prop="project">
        <el-input type="text" v-model=" formaddData.project "></el-input>
      </el-form-item>
      <el-form-item label="日期" prop="createdate">
        <el-date-picker
          type="date"
          placeholder="选择日期"
          v-model=" formaddData.createdate "
          :picker-options="pickerOptions "
          align="right"
        ></el-date-picker>
      </el-form-item>
      <el-form-item label="开始时间" prop="startdate">
        <el-date-picker
          placeholder="开始时间"
          v-model="formaddData.startdate"
          type="datetime"
          :picker-options="pickerOptions"
          align="right"
        ></el-date-picker>
      </el-form-item>
      <el-form-item label="结束时间" prop="enddate">
        <el-date-picker
          placeholder="结束时间"
          v-model=" formaddData.enddate"
          type="datetime"
          :picker-options="pickerOptions"
          align="right"
        ></el-date-picker>
      </el-form-item>
      <el-form-item label="加班费/补休" prop="status">
        <el-select clearable v-model = " formaddData.status " placeholder="补休">
          <el-option
            v-for="item of pay"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="加班事由" prop="overtimereason">
        <el-input type="text" v-model=" formaddData.overtimereason "></el-input>
      </el-form-item>
      <el-form-item label="加班地点" prop="overtimeposition">
        <el-select clearable v-model = " formaddData.overtimeposition " placeholder="选择地点">
          <el-option
            v-for="item of addr"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogAdd.show = false">取消</el-button>
      <el-button type="primary" @click="dialogFormAdd('formadd')">提交</el-button>
    </div>
  </el-dialog>
</template>
<script>
import axios from 'axios'
export default {
  name: 'AddUser',
  props: {
    dialogAdd: Object
  },
  data() {
    return {
      pickerOptions: {  //加班日期快捷键
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date())
          }
        },
        {
          text: '昨天',
          onClick(picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }],
        value1: '',
        value2: ''
      },
      addr: [{    //加班地点选择
        value: "总行",
        label: "总行"
      },
      {
        value: "泉舜",
        label: "泉舜"
      },
      {
        value: "翔安",
        label: "翔安"
      },
      {
        value: "虎园",
        label: "虎园"
      }],
      pay: [{
        value: '加班费',
        label: '加班费'
      },
      {
        value: '补休',
        label: '补休'
      }],
      formaddData: {   //新增表名
        project: '',
        createdate: '',
        startdate: '',
        enddate: '',
        status: '',
        overtimereason: '',
        overtimeposition: ''
      },
      formaddRules: {  //新增表规则
        project: [{ required: true, message: '项目不能为空', trigger: 'blur' }],
        createdate: [{ required: true, message: '日期不能为空', trigger: 'blur' }],
        startdate: [{ required: true, message: '开始时间不能为空', trigger: 'blur' }],
        enddate: [{ required: true, message: '结束时间不能为空', trigger: 'blur' }],
        overtimereason: [{ required: true, message: '加班事由不能为空', trigger: 'blur' }],
        overtimeposition: [{ required: true, message: '加班地点不能为空', trigger: 'blur' }]
      },
    }
  },
  methods:{
    dialogFormAdd(formadd) {
      this.$refs[formadd].validate((valid) => {
        if (valid) {
          let formaddData = {
            "formaddData":JSON.stringify(this.formaddData)
          }
          this.$axios({
            url: '/api/overtime/insertSelective',
            method: 'post',
            data: {
              'uid': parseInt(localStorage.getItem('uid')),
              'project': this.formaddData.project,
              'createdate': this.formaddData.createdate,
              'startdate': this.formaddData.startdate,
              'enddate': this.formaddData.enddate,
              'status': this.formaddData.status,
              'overtimereason': this.formaddData.overtimereason,
              'overtimeposition': this.formaddData.overtimeposition
            },
            headers:{
              'Authorization': 'Bearer '+localStorage.getItem('token')
            }
          }).then(res => {
            this.$message({
              type:"success",
              message:"添加信息成功"
            })
            this.dialogAdd.show = false
            this.$emit('update')
          })
          this.$refs[formadd].resetFields()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>



