<template>
  <el-dialog title="新增加班信息" :visible.sync="dialogEdit.show">
    <el-form
      :model="formeditData"
      label-width="100px"
      :rules="formeditRules"
      ref="formedit"
    >
      <el-form-item label="项目" prop="project">
        <el-input type="text" v-model=" formeditData.project "></el-input>
      </el-form-item>
      <el-form-item label="日期" prop="createdate">
        <el-date-picker
          type="date"
          placeholder="选择日期"
          v-model=" formeditData.createdate "
          :picker-options="pickerOptions "
          align="right"
          :disabled="true"
        ></el-date-picker>
      </el-form-item>
      <el-form-item label="开始时间" prop="startdate">
        <el-date-picker
          placeholder="开始时间"
          v-model="formeditData.startdate"
          type="datetime"
          :picker-options="pickerOptions"
          align="right"
        ></el-date-picker>
      </el-form-item>
      <el-form-item label="结束时间" prop="enddate">
        <el-date-picker
          placeholder="结束时间"
          v-model=" formeditData.enddate"
          type="datetime"
          :picker-options="pickerOptions"
          align="right"
        ></el-date-picker>
      </el-form-item>
      <el-form-item label="加班费/补休" prop="status">
        <el-select clearable v-model = " formeditData.status " placeholder="补休">
          <el-option
            v-for="item of pay"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="加班事由" prop="overtimereason">
        <el-input type="text" v-model=" formeditData.overtimereason "></el-input>
      </el-form-item>
      <el-form-item label="加班地点" prop="overtimeposition">
        <el-select clearable v-model = " formeditData.overtimeposition " placeholder="选择地点">
          <el-option
            v-for="item of addr"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogEdit.show = false">取消</el-button>
      <el-button type="primary" @click="dialogFormEdit('formedit')">提交</el-button>
    </div>
  </el-dialog>
</template>
<script>
import axios from 'axios'
export default {
  name: 'EditUser',
  props: {
    dialogEdit: Object,
    formeditData: Object
  },
  data() {
    return {
      pickerOptions: {  //加班日期快捷键
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date())
          }
        },
        {
          text: '昨天',
          onClick(picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }],
        value1: '',
        value2: ''
      },
      addr: [{    //加班地点选择
        value: "总行",
        label: "总行"
      },
      {
        value: "泉舜",
        label: "泉舜"
      },
      {
        value: "翔安",
        label: "翔安"
      },
      {
        value: "虎园",
        label: "虎园"
      }],
      pay: [{
        value: '加班费',
        label: '加班费'
      },
      {
        value: '补休',
        label: '补休'
      }],
      formeditRules: {  //修改表规则
        project: [{ required: true, message: '项目不能为空', trigger: 'blur' }],
        createdate: [{ required: true, message: '日期不能为空', trigger: 'blur' }],
        startdate: [{ required: true, message: '开始时间不能为空', trigger: 'blur' }],
        enddate: [{ required: true, message: '结束时间不能为空', trigger: 'blur' }],
        overtimereason: [{ required: true, message: '加班事由不能为空', trigger: 'blur' }],
        overtimeposition: [{ required: true, message: '加班地点不能为空', trigger: 'blur' }]
      },
    }
  },
  methods:{
    dialogFormEdit(formedit) {
      this.$refs[formedit].validate((valid) => {
        if (valid) {
          let formeditData = {
            "formeditData":JSON.stringify(this.formeditData)
          }
          this.$axios({
            url: '/api/overtime/updateSelective',
            method: 'post',
            data: {
              'id': parseInt(this.formeditData.id),
              'project': this.formeditData.project,
              'createdate': this.formeditData.createdate,
              'startdate': this.formeditData.startdate,
              'enddate': this.formeditData.enddate,
              'status': this.formeditData.status,
              'overtimereason': this.formeditData.overtimereason,
              'overtimeposition': this.formeditData.overtimeposition
            },
            headers:{
              'Authorization': 'Bearer '+localStorage.getItem('token')
            }
          }).then(res => {
            this.$message({
              type:"success",
              message:"修改信息成功"
            })
            this.dialogEdit.show = false
            this.$emit('update')
          })
          this.$refs[formedit].resetFields()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>



