<template>
<div>
  <!-- 头部 -->
  <div class="header">
    <div class="title">加班登记</div>
  </div>
  <!-- 侧边栏 -->
  <div class="aside">
    <img class="img-aside" src="@/assets/images/插图.png">
    <img class="img-title" src="@/assets/images/logo.png"/>
    <div class="icon">
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-edit-outline">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;加班登记</i>
    </div>
    <div class="user">
      <img class="user-img" src="@/assets/images/user-img.jpg">
      <p class="user-name">{{this.name}}</p>
    </div>
    <router-link to="/">
      <el-button class="exit" @click="handleExit()">退出登录</el-button>
    </router-link>
  </div>

  <!-- 主页面 -->
  <div class="main">
    <div class="content">
      <div class="user-main">
        <el-tabs v-model="activeName">
          <el-tab-pane label="加班明细" name="first">
            <el-col>
              <el-form :inline="true">
                <el-form-item label="统计月份:">
                  <el-date-picker  width="20" type="month" placeholder="选择登记时间"  v-model="month">
                  </el-date-picker>
                </el-form-item>
                <el-form-item style="float: right">
                  <el-button type="primary" icon="el-icon-edit-outline" @click="handleAdd()" >新增</el-button>
                </el-form-item >
              </el-form>
            </el-col>
            <div class="main-table">
              <el-table :data="searchUser(month)" border height="370" style="width: 100%">
                <el-table-column type="selection" width="35" align="center"></el-table-column>
                <el-table-column fixed type="index" label="序号" width="50" align="center"></el-table-column>
                <el-table-column prop="project" label="项目" width="115" align="center">
                  <template slot-scope="scope">
                    <span>{{ scope.row.project}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="createdate" label="日期" width="115" align="center">
                  <template slot-scope="scope">
                    <span>{{ scope.row.createdate | dateformat('YYYY-MM-DD')}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="startdate" label="开始时间" width="115" align="center">\
                  <template slot-scope="scope">
                    <span>{{ scope.row.startdate | dateformat('HH:mm')}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="enddate" label="结束时间" width="115" align="center">
                  <template slot-scope="scope">
                    <span>{{ scope.row.enddate | dateformat('HH:mm')}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="status" label="加班费/补休" width="115" align="center">
                  <template slot-scope="scope">
                    <span>{{ scope.row.status}}</span>
                  </template></el-table-column>
                <el-table-column prop="overtimereason" label="加班事由" width="115" align="center">
                  <template slot-scope="scope">
                    <span>{{ scope.row.overtimereason}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="overtimeposition" label="加班地点" width="115" align="center">
                  <template slot-scope="scope">
                    <span>{{ scope.row.overtimeposition}}</span>
                  </template>
                </el-table-column>
                <el-table-column fixed="right" label="操作" align="center">
                  <template slot-scope="scope">
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button size="mini" @click="handleEdit(scope.$index,scope.row)">编辑</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
            <AddUser :dialogAdd="dialogAdd" @update="getUserInfo"></AddUser>
            <EditUser :dialogEdit="dialogEdit" :formeditData="formeditData" @update="getUserInfo"></EditUser>
          </el-tab-pane>
          <!-- 加班汇总页面 -->
          <el-tab-pane label="加班汇总" name="second">
            <el-col>
              <el-form :inline="true">
                <el-form-item label="统计月份:">
                  <el-date-picker  width="20" type="month" placeholder="选择登记时间"  v-model="summonth">
                  </el-date-picker>
                </el-form-item>
              </el-form>
            </el-col>
            <div class="main-table">
              <el-table :data="sumData" border height="370" style="width: 100%" >
                <el-table-column fixed prop="month" label="月份" width="105" align="center"></el-table-column>
                <el-table-column prop="workdayB" label="平时补休小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="weekendB" label="周末补休小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="holidayB" label="节假日补休小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="workdayJ" label="平时加班小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="weekendJ" label="周末加班小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="holidayJ" label="节假日加班小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="allTime" label="加班小时数" width="105" align="center"></el-table-column>
                <el-table-column prop="mealpay" label="餐补" width="105" align="center"></el-table-column>
                <el-table-column prop="allCarpay" label="交通补贴" width="105" align="center"></el-table-column>
                <el-table-column prop="paysum" label="合计" width="80" align="center"></el-table-column>
              </el-table>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</div>
</template>
<script>
  import AddUser from './components/Add'
  import EditUser from './components/Edit'
  import axios from 'axios'
  import moment from 'moment'
  export default {
    name: 'UserMain',
    components: {
      AddUser,
      EditUser
    },
    data () {
      return {
        userData: [],
        dialogAdd: {     //是否显示新增信息
          show: false
        },
        dialogEdit: {   //是否显示编辑信息
          show: false
        },
        month: '',
        summonth: '',
        name: localStorage.getItem('name'),
        uid: localStorage.getItem('uid'),
        activeName: 'first',
        sumData:[],
        formeditData: {
          id: '',
          project: '',
          createdate: '',
          startdate: '',
          enddate: '',
          status: '',
          overtimereason: '',
          overtimeposition: ''
        }
      }
    },
    methods:{
      handleAdd () {    //显示新增信息
				this.dialogAdd.show = true
      },
      handleExit() {
        this.$router.replace('/')
      },
      handleEdit (index,row) {   //显示编辑信息
        this.dialogEdit.show = true
        this.formeditData = {
          id:row.id,
          project: row.project,
          createdate: row.createdate,
          startdate: row.startdate,
          enddate: row.enddate,
          status: row.status,
          overtimereason: row.overtimereason,
          overtimeposition: row.overtimeposition
        }
      },
      handleDelete(index,row) {  // 删除加班记录
        this.$confirm('此操作将删除此条加班记录，是否继续？','警告',{
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(() => {
          this.$axios({
            url: '/api/overtime/delete',
            method: 'delete',
            headers: {
              'Authorization': 'Bearer '+localStorage.getItem('token')
            },
            data:{
              'id': parseInt(row.id)
            }
          }).then(res =>{
            this.$message({
              type:"success",
              message:"删除信息成功"
            })
            this.getUserInfo()    //删除数据，更新视图
          })
        }).catch(()=>{})
      },
      getUserInfo () {   //获取所有的加班记录
        this.userData = []
        this.$axios({
          url: '/api/overtime/getAllOvertimeRecord',
          method: 'get',
          headers: {
            'Authorization': 'Bearer '+localStorage.getItem('token')
          }
        }).then(res =>{
          const data = res.data
          if(data) {
            data.forEach(item => {
              if(item.uid == this.uid){
                this.userData.push(item)
              }
            })
          }
        })
        this.getUsersum()
      },
      getUsersum(){
        this.sumData = []
        const Y = new Date(this.summonth).getFullYear()
        const M = new Date(this.summonth).getMonth()+1
        const month = Y*100+M
        this.$axios({
          url: '/api/overtime/Summary',
          method: 'post',
          headers: {
            'Authorization': 'Bearer '+localStorage.getItem('token')
          },
          data:{
            'uid': parseInt(localStorage.getItem('uid')),
            'month': parseInt(month)
          }
        }).then(res =>{
          const data = res.data
          var sum = {
            month: '',
            workdayB: '',
            workdayJ: '',
            weekendB: '',
            weekendJ: '',
            holidayB: '',
            holidayJ: '',
            allTime: '',
            allCarpay: '',
            mealpay: '',
            paysum: ''
          }
          sum.month = data.month
          sum.workdayB = data.workdayB
          sum.workdayJ = data.workdayJ
          sum.weekendB = data.weekendB
          sum.weekendJ = data.weekendJ
          sum.holidayB = data.holidayB
          sum.holidayJ = data.holidayJ
          sum.allTime = data.allTime
          sum.allCarpay = data.allCarpay
          sum.mealpay = data.allPay
          sum.paysum = data.allCarpay+data.allPay
          this.sumData.push(sum)
        })

      },
      searchUser(month){
        return this.userData.filter(item =>{
          if(!month){
            return item
          }
          else if(month){
            const Y1 = new Date(this.month).getFullYear()
            const M1 = new Date(this.month).getMonth() + 1
            const Y2 = new Date(item.createdate).getFullYear()
            const M2  = new Date(item.createdate).getMonth() + 1
            if(Y1==Y2 && M1==M2){
              return item
            }
          }
        })
      },
      searchUsersum(month) {

      }
    },
    mounted () {
      this.getUserInfo()
    }
  }
</script>
<style lang="stylus" scoped>
  .header
    position: absolute
    top: 0
    right: 0
    left: 200px
    height: 56px
    background: #fff
    .title
      margin-left: 28px
      line-height: 56px
      font-size: 16px
      color: rgba(0,0,0,0.85)
  .aside
    position:absolute
    top: 0
    left: 0
    bottom: 0
    width: 200px
    background: #1B202E
    .exit
      position: absolute
      bottom: 50px
      left: 51px
      opacity: 0.4
      color: #fff
      background-color: transparent
    .img-aside
      position: absolute
      bottom: 0
      width: 200px
    .img-title
      position: absolute
      top: 20px
      left: 27px
    .icon
      position: absolute
      top: 75px
      height: 42px
      width: 100%
      text-align: left
      font-size: 15px
      line-height: 42px
      color: #FFFFFF
      background-image: linear-gradient(90deg, #59E3FF 0%, #1F9CFF 100%)
    .user
      position: absolute
      bottom: 200px
      left: 70px
      right: 70px
      .user-img
        position: absolute
        width: 60px
        height: 60px
        border-radius: 100px
        width: 100%
      .user-name
        position: absolute
        top: 60px
        width: 80px
        left: -10px
        height: 15px
        font-size: 15px
        line-height: 15px
        text-align: center
        color: #fff
  .main
    position: absolute
    top: 56px
    left: 200px
    bottom: 0
    right: 0
    background: #eee
    .content
      position: absolute
      top: 24px
      left: 24px
      bottom: 24px
      right: 24px
      background: #fff
      .user-main
        margin: 5px 20px
        .main-table
          text-align: center
</style>



